.fMNmv .uui-calendar-content {
  font-family: "Sans Regular", Arial, sans-serif;
}
.fMNmv .uui-calendar-weekday {
  font-family: "Sans Regular", Arial, sans-serif;
  color: #6C6F80;
  width: 34px;
  font-size: 12px;
  line-height: 24px;
  text-align: center;
}
.fMNmv .uui-calendar-day-cell {
  color: #303240;
  text-align: center;
  padding: 0;
  height: 36px;
  width: 36px;
  display: flex;
  position: relative;
}
.fMNmv .uui-calendar-day-cell:focus {
  outline: none;
}
.fMNmv .uui-calendar-day-wrapper {
  height: 30px;
  width: 30px;
  font-size: 14px;
  line-height: 30px;
  margin: auto;
  cursor: default;
}
.fMNmv .uui-calendar-day {
  border-radius: 50%;
  height: 36px;
  width: 36px;
  position: absolute;
  left: 0;
  top: 0;
  line-height: 34px;
  border: 1px solid transparent;
  box-sizing: border-box;
}
.fMNmv .uui-calendar-clickable-day:hover:not(.uui-calendar-filtered-day) {
  cursor: pointer;
}
.fMNmv .uui-calendar-clickable-day:hover:not(.uui-calendar-filtered-day) .uui-calendar-day {
  border: 1px solid #008ACE;
  background-color: #FFFFFF;
  z-index: 1;
}
.fMNmv .uui-calendar-clickable-day:hover:not(.uui-calendar-filtered-day).uui-calendar-selected-day .uui-calendar-day {
  border: 1px solid #0079b5;
  background-color: #0079b5;
}
.fMNmv .uui-calendar-current-day.uui-calendar-day-wrapper {
  color: #008ACE;
  font-family: "Sans Semibold", Arial, sans-serif;
}
.fMNmv .uui-calendar-current-day.uui-calendar-day-wrapper .uui-calendar-day::after {
  position: absolute;
  content: "";
  height: 4px;
  width: 4px;
  border-radius: 50%;
  background-color: #008ACE;
  bottom: 3px;
  left: calc(50% - 2px);
}
.fMNmv .uui-calendar-selected-day.uui-calendar-day-wrapper .uui-calendar-day {
  background-color: #008ACE;
  color: #FFFFFF;
}
.fMNmv .uui-calendar-selected-day.uui-calendar-day-wrapper .uui-calendar-day::after {
  background-color: #FFFFFF;
}
.fMNmv .uui-calendar-selected-day.uui-calendar-clickable-day:hover {
  cursor: pointer;
}
.fMNmv .uui-calendar-day-holiday {
  color: #6C6F80;
}
.fMNmv .uui-calendar-filtered-day.uui-calendar-day-wrapper {
  color: #ACAFBF;
}
.fMNmv .uui-calendar-filtered-day.uui-calendar-day-wrapper:hover {
  cursor: default;
  background-color: transparent;
}
.fMNmv .uui-calendar-filtered-day.uui-calendar-day-wrapper.uui-calendar-selected-day .uui-calendar-day {
  background-color: #B3B6C3;
}