.o5eie.uui-blocker-container .uui-blocker {
  background-color: rgba(245, 246, 250, 0.5019607843);
}
.o5eie.uui-blocker-container .uui-blocker-enter {
  background-color: rgba(245, 246, 250, 0);
}
.o5eie.uui-blocker-container .uui-blocker-enter-active {
  background-color: rgba(245, 246, 250, 0.5019607843);
  transition: opacity 1000ms ease-out, background-color 2000ms ease-out;
}
.o5eie.uui-blocker-container .uui-blocker-exit {
  background-color: rgba(245, 246, 250, 0.5019607843);
  pointer-events: none;
  transition: opacity 1000ms ease-out, background-color 200ms ease-out;
}
.o5eie.uui-blocker-container .uui-blocker-exit-active {
  background-color: rgba(245, 246, 250, 0);
}