.kI-AK {
  min-width: 36px;
  color: var(--text-color);
  fill: var(--text-color);
  background-color: transparent;
  cursor: pointer;
  padding-left: 12px;
  padding-right: 12px;
  position: relative;
  bottom: -1px;
  display: flex;
  align-items: center;
  font-family: "Sans Semibold", Arial, sans-serif;
  font-weight: normal;
  border-bottom: 3px solid transparent;
}
.kI-AK.uui-has-left-icon .uui-caption {
  padding-left: 6px;
}
.kI-AK.uui-has-right-icon .uui-caption {
  padding-right: 6px;
}
.kI-AK:hover, .kI-AK:focus {
  color: var(--text-color-active);
  fill: var(--text-color-active);
}
.kI-AK.uui-active {
  color: var(--text-color-active);
  fill: var(--text-color-active);
  border-color: var(--text-color-active);
}
.kI-AK.uui-active {
  border-color: var(--text-color-active);
}
.kI-AK.uui-disabled {
  color: var(--text-color-disabled);
  fill: var(--text-color-disabled);
  background-color: transparent;
}
.kI-AK.uui-disabled:hover, .kI-AK.uui-disabled:focus {
  color: var(--text-color-disabled);
  fill: var(--text-color-disabled);
}
.kI-AK.uui-disabled.uui-active {
  color: var(--text-color-disabled);
  fill: var(--text-color-disabled);
  border-color: var(--text-color-disabled);
}
.kI-AK._5Vs8V::after {
  content: "";
  margin-bottom: 18px;
  height: 6px;
  width: 6px;
  background: #E54322;
  box-shadow: 0 1px 8px 0 #E54322;
  border-radius: 50%;
}
.kI-AK .uui-count {
