/* Main Colors */
/* Gray */
.yCDJ- {
  --background-color: #474A59;
  --color: white;
  --fill: #ACAFBF;
  --placeholder-color: #ACAFBF;
  flex-basis: 200px;
  max-width: 500px;
  height: 36px;
  min-width: 36px;
  border-radius: 18px;
  background-color: var(--background-color);
  fill: var(--fill);
  padding: 0 6px;
  margin: 0 12px;
  transition: background-color 0.3s ease-out, flex-basis 0.3s ease-in-out;
}
.yCDJ- .uui-input {
  color: var(--color);
  font-family: "Sans Regular", Arial, sans-serif;
}
.yCDJ- .uui-input::placeholder {
  color: var(--placeholder-color);
}
.yCDJ- .uui-caption,
.yCDJ- .uui-input {
  padding: 8px 6px;
  line-height: 18px;
  font-size: 14px;
}
.yCDJ- .uui-icon svg {
  height: 16px;
  max-width: 16px;
}
.yCDJ- .uui-icon-cancel svg {
  height: 24px;
  max-width: 24px;
}
.yCDJ- .uui-icon-cancel:hover {
  cursor: pointer;
}
.yCDJ-.uui-input-box {
  min-height: 36px;
}