/* Main Colors */
/* Gray */
.Dwfci .hero-header, .Dwfci h1, .Dwfci h2, .Dwfci h3, .Dwfci h4, .Dwfci h5, .Dwfci h6 {
  margin: 0;
}
.Dwfci .hero-header {
  font-family: "Museo Slab", "Roboto Slab", Arial, sans-serif;
  margin: 30px 0;
  font-size: 66px;
  line-height: 72px;
}
.Dwfci .hero-header, .Dwfci .promo-header {
  color: #1D1E26;
  text-align: center;
}
.Dwfci .promo-header, .Dwfci h1, .Dwfci h2, .Dwfci h3 {
  font-family: "Museo Sans", "Sans Semibold", Arial, sans-serif;
}
.Dwfci .promo-header {
  text-transform: uppercase;
}
.Dwfci h4, .Dwfci h5, .Dwfci h6 {
  font-family: "Sans Semibold", Arial, sans-serif;
}
.Dwfci h1, .Dwfci h2, .Dwfci h3, .Dwfci h4, .Dwfci h5, .Dwfci h6 {
  margin-bottom: 0.5em;
  margin-top: 1.2em;
  line-height: 1.35em;
}
.Dwfci h1 {
  font-size: 36px;
}
.Dwfci h2 {
  font-size: 30px;
  letter-spacing: 0.1px;
}
.Dwfci h2.promo-header {
  letter-spacing: 2px;
}
.Dwfci h3 {
  font-size: 24px;
}
.Dwfci h4 {
  font-size: 18px;
  letter-spacing: 0.1px;
}
.Dwfci h5 {
  font-size: 16px;
}
.Dwfci h6 {
  font-size: 14px;
}

