/* Main Colors */
/* Gray */
.bBlbk .hero-header, .bBlbk h1, .bBlbk h2, .bBlbk h3, .bBlbk h4, .bBlbk h5, .bBlbk h6 {
  margin: 0;
}
.bBlbk .hero-header {
  font-family: "Museo Slab", "Roboto Slab", Arial, sans-serif;
  margin: 30px 0;
  font-size: 66px;
  line-height: 72px;
}
.bBlbk .hero-header, .bBlbk .promo-header {
  color: #1D1E26;
  text-align: center;
}
.bBlbk .promo-header, .bBlbk h1, .bBlbk h2, .bBlbk h3 {
  font-family: "Museo Sans", "Sans Semibold", Arial, sans-serif;
}
.bBlbk .promo-header {
  text-transform: uppercase;
}
.bBlbk h4, .bBlbk h5, .bBlbk h6 {
  font-family: "Sans Semibold", Arial, sans-serif;
}
.bBlbk h1, .bBlbk h2, .bBlbk h3, .bBlbk h4, .bBlbk h5, .bBlbk h6 {
  margin-bottom: 0.5em;
  margin-top: 1.2em;
  line-height: 1.35em;
}
.bBlbk h1 {
  font-size: 36px;
}
.bBlbk h2 {
  font-size: 30px;
  letter-spacing: 0.1px;
}
.bBlbk h2.promo-header {
  letter-spacing: 2px;
}
.bBlbk h3 {
  font-size: 24px;
}
.bBlbk h4 {
  font-size: 18px;
  letter-spacing: 0.1px;
}
.bBlbk h5 {
  font-size: 16px;
}
.bBlbk h6 {
  font-size: 14px;
}

.xcFJW ol, .xcFJW ul {
  padding: 0;
}
.xcFJW li {
  margin-left: 1.25em;
  line-height: 1.5;
}
.xcFJW pre {
  padding: 1.25em;
  color: #F5F6FA;
  background-color: #474A59;
  font-family: monospace;
  white-space: pre-wrap;
  word-wrap: normal;
}
.xcFJW img {
  max-width: 100%;
}
.xcFJW figure {
  margin: 0;
}
.xcFJW figure figcaption {
  font-size: 0.9em;
  opacity: 0.9;
  text-align: center;
}
.xcFJW p {
  margin: 0.5em 0;
  line-height: 1.5;
}

.hl2dR b, .hl2dR strong {
  font-family: "Sans Semibold", Arial, sans-serif;
}
.hl2dR i, .hl2dR em {
  font-family: "Sans Italic", Arial, sans-serif;
}
.hl2dR a {
  display: inline-flex;
  align-items: center;
  color: #008ACE;
}
.hl2dR a:visited {
  color: #5214CC;
}
.hl2dR a:visited:hover {
  color: #3F0F9E;
}
.hl2dR a:hover {
  text-decoration: underline;
  color: #0079b5;
}
.hl2dR code {
  padding: 0.125em 0.25em;
  color: #303240;
  background-color: #E1E3EB;
  font-family: "Roboto Mono", Arial, sans-serif;
  white-space: pre-wrap;
  word-wrap: normal;
}

