/* Main Colors */
/* Gray */
.tWFAi .hero-header, .tWFAi h1, .tWFAi h2, .tWFAi h3, .tWFAi h4, .tWFAi h5, .tWFAi h6 {
  margin: 0;
}
.tWFAi .hero-header {
  font-family: "Museo Slab", "Roboto Slab", Arial, sans-serif;
  margin: 30px 0;
  font-size: 66px;
  line-height: 72px;
}
.tWFAi .hero-header, .tWFAi .promo-header {
  color: #1D1E26;
  text-align: center;
}
.tWFAi .promo-header, .tWFAi h1, .tWFAi h2, .tWFAi h3 {
  font-family: "Museo Sans", "Sans Semibold", Arial, sans-serif;
}
.tWFAi .promo-header {
  text-transform: uppercase;
}
.tWFAi h4, .tWFAi h5, .tWFAi h6 {
  font-family: "Sans Semibold", Arial, sans-serif;
}
.tWFAi h1, .tWFAi h2, .tWFAi h3, .tWFAi h4, .tWFAi h5, .tWFAi h6 {
  margin-bottom: 0.5em;
  margin-top: 1.2em;
  line-height: 1.35em;
}
.tWFAi h1 {
  font-size: 36px;
}
.tWFAi h2 {
  font-size: 30px;
  letter-spacing: 0.1px;
}
.tWFAi h2.promo-header {
  letter-spacing: 2px;
}
.tWFAi h3 {
  font-size: 24px;
}
.tWFAi h4 {
  font-size: 18px;
  letter-spacing: 0.1px;
}
.tWFAi h5 {
  font-size: 16px;
}
.tWFAi h6 {
  font-size: 14px;
}

