.Bmpq8 {
  --text-color: #474A59;
  --icon-color: #6C6F80;
}
.Bmpq8.uui-timepicker-container {
  display: flex;
  padding: 15px 12px;
}
.Bmpq8.uui-timepicker-container .uui-timepicker-input.uui-input-box {
  margin: 6px 0;
  width: 36px;
  height: 36px;
}
.Bmpq8.uui-timepicker-container .uui-timepicker-input > input {
  text-align: center;
  font-size: 24px;
  line-height: 24px;
  color: var(--text-color);
  font-family: "Sans Regular", Arial, sans-serif;
}
.Bmpq8.uui-timepicker-container .uui-timepicker-item {
  position: relative;
  padding: 9px 24px;
}
.Bmpq8.uui-timepicker-container .uui-timepicker-item:first-child::after {
  content: ":";
  position: absolute;
  top: 35px;
  left: 81px;
  color: var(--text-color);
  font-size: 24px;
  line-height: 30px;
}
.Bmpq8.uui-timepicker-container .vNmXW {
  align-items: center;
  fill: var(--icon-color);
}
.Bmpq8.uui-timepicker-container .vNmXW:hover {
  cursor: pointer;
}
.Bmpq8.uui-timepicker-container .uui-timepicker-icon-up {
  align-items: center;
  fill: var(--icon-color);
  transform: rotate(180deg);
}
.Bmpq8.uui-timepicker-container .uui-timepicker-icon-down {
  align-items: center;
  fill: var(--icon-color);
}

._3Wc4d {
